@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.full-wh {
  height: 100vh;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.login-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  /* background-color: #fff; */
  border: 1px solid #ee5935;
  border-radius: 8px;
}

.tool-info {
  width: 55%;
  display: flex;
  flex-direction: column;
  /* background-color: #ee5935; */
  height: 100%;
  align-items: center;
  justify-content: center;
  /* color: white; */
  background: url(/static/media/99815.52c3fb69.png);
  background-size: cover;
}

.login-form-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 60%;
  align-items: center;
  justify-content: center;
  background-color: #f5f5f5;
}

.input-control {
  padding: 5px 10px;
  margin-bottom: 0.5rem;
}

.enter_cred {
  position: relative;
  bottom: 20px;
}

.login-form-container .info {
  position: absolute;
  bottom: 25px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: helvetica;
  letter-spacing: 1px;
  color: #353535;
  font-size: 15px;
  transition: all 0.5s;
}
body,
html {
  overflow-x: hidden;
  font-weight: 400;
}

#root {
  height: 100vh;
}

/*utility*/
.wd40 {
  width: 40%;
}

.wd100 {
  width: 100%;
}
.rel {
  position: relative;
}

.nav-bg {
  background-color: #3c3c3c;
}
.link {
  color: inherit;
  text-decoration: none;
}
li {
  margin-right: 1rem;
  list-style: none;
  border-right: 0.08rem solid #fff;
  padding-right: 5px;
}

li:hover {
  cursor: pointer;
  text-decoration: underline;
}

.btn-action {
  background-color: #ee5935;
  color: white;
  border: none;
  padding: 9px 10px;
  width: 100%;
  cursor: pointer;
  border-radius: 4px;
}

.btn-action:hover {
  background-color: #d85333;
}

.flex-center-col {
  display: flex;

  align-items: center;
  flex-direction: column;
}

.flex-col {
  display: flex;

  flex-direction: column;
}

.flex-center-row {
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}

.flex-justify-center-col {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.flex-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.sec-heading {
  font-size: 1.5rem;
  font-weight: 500;
}
.sub-heading {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.text-center {
  text-align: center;
}

.form-label {
  margin: 0.8rem 0rem;
}

.info {
  color: #ee575d;
}

nav {
  width: 100vw;
  display: flex;
  padding: 1.2rem;
  flex-direction: row;
  justify-content: space-between;
  color: #f5f5f5;
  border-bottom: 1px solid rgb(236, 236, 236);
  height: 8%;
}

.logo {
  color: #ee5935;
  font-size: 1.3rem;
}

.menu ul {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.menu ul li {
  margin-right: 1rem;
  list-style: none;
  border-right: 0.08rem solid #fff;
  padding-right: 5px;
  color: #ee5935;
}
.menu ul li:hover {
  cursor: pointer;
  color: #b33e04;
  text-decoration: underline;
}

.hr-section {
  height: 92%;
  background: #fff;
  display: flex;
  flex-direction: column;
}

.hr-menu {
  width: 100%;
  padding: 1.11rem 0rem;
  display: flex;
  justify-content: flex-end;
  box-shadow: 0px 2px 5px #d5d5d5;
}

.hr-menu ul {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.hr-menu li {
  color: #327e8b;
}

#addEmpFormSec form {
  padding: 2vw;
}
#addEmpFormSec form input:not([type="submit"]):not([type="button"]),
#addEmpFormSec form select {
  border: 2px solid #cecbcb;
  margin-left: 0.2vw;
  padding: 0.3vw;
  border-radius: 0.3vw;
}

.form-section {
  background: #dfecee;
  border-radius: 4px;
  margin: 1rem;
  padding: 1.2rem;
  min-width: 40%;
}

.left-menu-container {
  padding: 2rem 0rem 0rem 1rem;
  width: 20%;
  border-right: 1px solid #e4e4e4;
  height: 100%;
}

.hr-body-wrapper {
  display: flex;
  flex-direction: row;
  height: 100%;
}

.menu-items li {
  padding: 1rem 0rem 1rem 1rem;
  margin: 0;
  border-radius: 4px;
  font-weight: 500;
}

.menu-items li.active,
.menu-items li:hover {
  background: #dfecee;
}

.flex-grow-1 {
  flex-grow: 1;
}

.right-container {
  padding-top: 2rem;
}

.progress-bar {
  width: 50%;
  margin: 1rem auto;
  box-shadow: 1px 1px 0px 1px inset #dadada;
  background: #dfecee;
}
.show-progress {
  width: 25%;
  background: #ee5935;
  height: 20px;
  color: #fff;
  padding-left: 1px;
}

